<template>
    <div id="productModal" class="modal fade" tabindex="-1" aria-labelledby="productModalLabel"
        aria-hidden="true" ref="modal">
        <div class="modal-dialog modal-xl">
          <div class="modal-content border-0">
            <div class="modal-header bg-dark text-white">
              <h5 id="productModalLabel" class="modal-title">
                <span v-if="isNew">新增產品</span>
                <span v-else>編輯產品</span>
              </h5>
              <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
            </div>
            <div class="modal-body">
              <div class="row">
                <div class="col-sm-4">
                  <div class="mb-3">
                    <label for="imageUrl" class="form-label">輸入圖片網址</label>
                    <input v-model="tempProduct.imageUrl" type="text" class="form-control mb-2" placeholder="請輸入圖片連結">
                  </div>
                  <div class="mb-3">
                    <label for="customFile" class="form-label">
                      或 上傳圖片
                      <i class="fas fa-spinner fa-spin" v-if="status.fileUploading"></i>
                    </label>
                    <input type="file" id="customFile" class="form-control" ref="fileInput"  @change="uploadFile">
                  </div>
                  <img class="img-fluid" :src="tempProduct.imageUrl" />
                  <!-- 延伸技巧，多圖 -->
                   <div class="mt-5" v-if="tempProduct.imagesUrl">
                     <div class="mb-3" v-for="(image, key) in tempProduct.imagesUrl" :key="key">
                       <input type="url" class="form-control form-control" v-model="tempProduct.imagesUrl[key]" placeholder="請輸入連結"/>
                       <div>
                          <img class="img-fluid" :src="image"/>
                       </div>
                       <button type="button" class="btn btn-outline-danger" @click="tempProduct.imagesUrl.splice(key, 1)">
                        移除
                      </button>
                     </div>
                     <div v-if="tempProduct.imagesUrl[tempProduct.imagesUrl.length - 1] || !tempProduct.imagesUrl.length">
                  <button class="btn btn-outline-primary btn-sm d-block w-100" @click="tempProduct.imagesUrl.push('')">
                    新增圖片
                  </button>
                </div>
              </div>
            </div>
                <div class="col-sm-8">
                  <div class="mb-3">
                    <label for="title" class="form-label">標題</label>
                    <input id="title" v-model="tempProduct.title" type="text" class="form-control" placeholder="請輸入標題">
                  </div>
                  <div class="row">
                    <div class="mb-3 col-md-6">
                      <label for="category" class="form-label">分類</label>
                      <input id="category" v-model="tempProduct.category" type="text" class="form-control" placeholder="請輸入分類">
                    </div>
                    <div class="mb-3 col-md-6">
                      <label for="price" class="form-label">單位</label>
                      <input id="unit" v-model="tempProduct.unit" type="text" class="form-control" placeholder="請輸入單位">
                    </div>
                  </div>
                  <div class="row">
                    <div class="mb-3 col-md-6">
                      <label for="origin_price" class="form-label">原價</label>
                      <input id="origin_price"  v-model.number="tempProduct.origin_price" type="number"  min="0" class="form-control" placeholder="請輸入原價">
                    </div>
                    <div class="mb-3 col-md-6">
                      <label for="price" class="form-label">售價</label>
                      <input id="price" v-model.number="tempProduct.price"  type="number" min="0" class="form-control" placeholder="請輸入售價">
                    </div>
                  </div>
                  <hr>
                  <div class="mb-3">
                    <label for="description" class="form-label">產品描述</label>
                    <textarea id="description" v-model="tempProduct.description"  type="text" class="form-control" placeholder="請輸入產品描述"
                    rows=5>
                  </textarea>
                  </div>
                  <div class="mb-3">
                    <label for="content" class="form-label">說明內容</label>
                    <textarea id="description"  v-model="tempProduct.content" type="text" class="form-control" placeholder="請輸入說明內容"
                    rows=8>
                  </textarea>
                  </div>
                  <div class="mb-3">
                    <div class="form-check">
                      <input id="is_enabled" v-model="tempProduct.is_enabled" class="form-check-input" type="checkbox" :true-value="1" :false-value="0">
                      <label class="form-check-label" for="is_enabled">是否啟用</label>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="modal-footer">
              <button type="button" class="btn btn-outline-secondary" data-bs-dismiss="modal">
                取消
              </button>
              <button type="button" class="btn btn-primary"  @click="$emit('update-product',tempProduct)">
                      確認
              </button>
            </div>
          </div>
        </div>
    </div>
</template>

<script>
import modalMixin from '@/mixins/modalMixin'

export default {
  data () {
    return {
      status: {},
      modal: '',
      tempProduct: {}
    }
  },
  props: {
    product: {
      type: Object,
      default () { return {} }
    },
    isNew: {
      type: Boolean,
      default: false
    }
  },
  mixins: [modalMixin],
  inject: ['emitter'],
  watch: {
    product () {
      this.tempProduct = this.product
      if (!this.tempProduct.imagesUrl) {
        this.tempProduct.imagesUrl = []
      }
      if (!this.tempProduct.imageUrl) {
        this.tempProduct.imageUrl = ''
      }
    }
  },
  methods: {
    uploadFile () {
      const uploadedFile = this.$refs.fileInput.files[0]
      const formData = new FormData()
      formData.append('file-to-upload', uploadedFile)
      const url = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/admin/upload`
      this.status.fileUploading = true
      this.$http.post(url, formData, {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      })
        .then((res) => {
          this.status.fileUploading = false
          if (res.data.success) {
            this.tempProduct.imageUrl = res.data.imageUrl
            this.$refs.fileInput.value = ''
            this.emitter.emit('push-message', {
              style: 'success',
              title: '圖片上傳結果',
              content: res.data.message
            })
          } else {
            this.$refs.fileInput.value = ''
            this.emitter.emit('push-message', {
              style: 'danger',
              title: '圖片上傳結果',
              content: res.data.message
            })
          }
        })
        .catch((error) => {
          this.status.fileUploading = false
          this.$httpMessageState(error.response, '圖片失敗')
        })
    }
  }
}
</script>
